import {Form,Button,Row,Col,Modal} from 'react-bootstrap';
import {MdDelete,MdAddCircle,MdCancel} from 'react-icons/md';
import { default as ReactSelect } from "react-select";
import {useState,useEffect} from 'react';
import { components } from "react-select";
const Option = (props) => {
    return (
      <div>
        <components.Option {...props}>
          <input
            type="checkbox"
            checked={props.isSelected}
            onChange={() => null}
          />{" "}
          <label>{props.label}</label>
        </components.Option>
      </div>
    );
  };
function RenderPriceCondition(props){
    var defaultOperator;
    var defaultPrice;
    if(props.defaultPriceCondition){
        defaultOperator = props.defaultPriceCondition.operator;
        defaultPrice = props.defaultPriceCondition.price;
    }
    const schemeNum = props.schemeNum;
    return (
        <Form.Group className="mb-3" controlId="formBasicOperator">
            <Form.Label>Select the Condition</Form.Label>
            <Row>
                <Col md={2} xs={4}>
                    <Form.Control
                    defaultValue = {defaultOperator}
                    as="select" name={"priceOperator_" + schemeNum} >
                    <option value=">=">{">="}</option>
                    <option value="=">=</option>
                    <option value="<=">{"<="}</option>
                    </Form.Control>
                </Col>
                <Col>
                    <Form.Control type="number" name={"condValue_" + schemeNum} defaultValue = {defaultPrice} placeholder="Enter Condition Value" />
                </Col>
            </Row>  
        </Form.Group>
    )
}
function RenderModelCondition(props){
    const schemeNum = props.schemeNum;
    // const options = props.options;
    const setModelOptionSelected = props.setModelOptionSelected;
    var modelOptionSelected = props.modelOptionSelected;
    function handleModelChange (e) {
        // e.preventDefault();
        console.log(e);
        setModelOptionSelected(e);
      };
    // var options = [{value:'',label:''}];
    var options = [];
    props.options.map((item)=>options.push({value:item,label:item}));
    var modelOptionName = "modelOptionsSelected_" + schemeNum;
    // const selectedOption = props.modelList[0];
    //mobile model condition allow
    return(
            <ReactSelect
            closeMenuOnSelect={false}
            options={options}
            isMulti
            isSearchable
            hideSelectedOptions={false}
            components={{
                Option
            }}
            onChange={e => {handleModelChange(e);}}
            allowSelectAll={true}
            value={modelOptionSelected}
            name = {modelOptionName}
            />
    )
}
function RenderCondition(props){
    const schemeNum = props.schemeNum;
    if(props.condType==="No"){
        return (<div></div>)
    }
    else if(props.condType==="Price_Condition"){
        return <RenderPriceCondition schemeNum={schemeNum} defaultPriceCondition = {props.defaultPriceCondition}/>
    }
    else if(props.condType==="Model_Condition"){
        return <RenderModelCondition options={props.modelList} modelOptionSelected={props.modelOptionSelected} setModelOptionSelected={props.setModelOptionSelected} schemeNum={schemeNum}/>
    }
    else {return(<div></div>);}
}

function SchemeBoxComponent(props){
    const removeSchemeBox = props.removeSchemeBox;
    const schemeNum = props.schemeNum;
    const modelList = props.modelList;
    var startDate = props.startDate;
    var endDate = props.endDate;
    var default_cond = "No";
    var default_modelOptionSelected = [];
    // var default_modelOptionSelected = [{value:'',label:''}];

    var default_val = [];
   
    if(props.scheme){
        const i = props.schemeNum;
        default_val = {
        schemeName : props.scheme.name[i],
        startDate : props.scheme.start_date[i].split("T")[0],
        endDate : props.scheme.end_date[i].split("T")[0],
        cond : props.scheme.condition_type[i],
        price_condition : {
            operator : props.scheme.price_condition[i].operator,
            price : props.scheme.price_condition[i].price
        },
        creditType : props.scheme.creditValue[i].creditType,
        creditValue : props.scheme.creditValue[i].creditValue,
        modelOptionSelected : props.scheme.model_condition[i]
        }
        default_cond = default_val.cond;
        if(default_val.modelOptionSelected!== null){
        for(let p = 0 ; p< default_val.modelOptionSelected.length; p++){
            default_modelOptionSelected.push({value : default_val.modelOptionSelected[p],label : default_val.modelOptionSelected[p]});
            }
        }
        
    }
    if(default_val.length !== 0){
        startDate = default_val.startDate;
        endDate = default_val.endDate;
    }

    const [modelOptionSelected,setModelOptionSelected] = useState(default_modelOptionSelected);
    const [condType,setCondType] = useState(default_cond);
    if(condType){
        return (
            <div className="scheme-box">
                {/* {default_val.length === 0 ? <Button className="button-remove-scheme" onClick={(e) => removeSchemeBox()}><MdCancel size={30}/></Button> : <div></div>} */}
                <Form.Group className="mb-3" controlId="formBasicName">
                    <Form.Label controlId="floatingInputGrid" label="Email address">
                    <Form.Control required type="name" name={"schemeName_" + schemeNum} placeholder="Enter Scheme Name" defaultValue={default_val.schemeName}/>
                    </Form.Label>
                    {/* <Form.Text className="text-muted">
                    Create a new name for scheme to help you track later on
                    </Form.Text> */}
    
                </Form.Group>
                <Form.Group className="mb-3" controlId="start_date">
                    <Form.Label>Start Date</Form.Label>
                    <Form.Control required type="date" name={"startDate_" + schemeNum} placeholder="Scheme Start Date" defaultValue={startDate}/>
                </Form.Group>
                <Form.Group className="mb-3" controlId="end_date">
                    <Form.Label>End Date</Form.Label>
                    <Form.Control required type="date" name={"endDate_" + schemeNum} placeholder="Scheme End Date" defaultValue={endDate}/>
                </Form.Group>
    
                <Form.Group className="mb-3" controlId="formBasicCondType">
                    <Form.Label>Select Condition Type</Form.Label>
                    <Form.Control required
                    as="select"
                    name={"condType_" + schemeNum}
                    value={condType}
                    onChange={e => {
                        setCondType(e.target.value);
                    }}
                    >
                    <option value="No">No Condition</option>
                    <option value="Price_Condition">Price Condition</option>
                    <option value="Model_Condition">Model Condition</option>
                    </Form.Control>
                </Form.Group>
                <RenderCondition condType={condType} defaultPriceCondition = {default_val.price_condition} modelList={modelList} modelOptionSelected={modelOptionSelected} setModelOptionSelected={setModelOptionSelected} schemeNum={schemeNum}/>
                <Form.Group className="mb-3" controlId="formBasicOperator">
                <Form.Label>Offered Amount</Form.Label>
                    <Row>
                        <Col md={2} xs={4}>
                            <Form.Control
                            required
                            as="select" name={"creditType_" + schemeNum} defaultValue={default_val.creditType}>
                            <option value="%">%</option>
                            <option value="flat">Flat</option>
                            </Form.Control>
                        </Col>
                        <Col>
                            <Form.Control required type="number" step="0.01" defaultValue={default_val.creditValue} name={"creditValue_" + schemeNum} placeholder="Enter Value" />
                        </Col>
                    </Row>  
                </Form.Group>
            </div>
        )
    }
    else{
        return(<div></div>)
    }
    
}

export default SchemeBoxComponent;