import {Form,Button,Row,Col,Modal} from 'react-bootstrap';
import { SheetJSFT } from '../shared/file_types';
import {useState,useEffect} from 'react';
import XLSX from 'xlsx';    
function UploadFileComponent(props){
    const [file,setFile] = useState();
    const [modelShow,setModelShow] = useState(); 
    const [excelData,setExcelData] = useState();
    const [fileHeader,setFileHeader] = useState();
    const [userHeaderSubmit,setUserHeaderSubmit] = useState();
    var fileHeaderOptions;
    useEffect(() => {
        setFileHeader(null);
        setModelShow(false);
      }, []);
    useEffect(() => {
        if(file){
            var id = 'activation_file_upload' + props.fileNum;
            document.getElementById(id).click();
        }
    }, [file]);

    function   handleActivationFileChange(e) {
        const files = e.target.files;
        if (files && files[0]) {
            console.log(files[0]);
            setFile(files[0]);
            
        };
        
    };
    if(fileHeader) fileHeaderOptions = fileHeader.map((head)=>{return (<option key = {head} value={head}>{head}</option>)}); 
    function   handleActivationUpload(e) {
        e.preventDefault();
        /* Boilerplate to set up FileReader */
        const reader = new FileReader();
        const rABS = !!reader.readAsBinaryString;
        // console.log(file);
        try{
            reader.onload = (e) => {
            /* Parse data */
                const bstr = e.target.result;
                const wb = XLSX.read(bstr, {
                    type: rABS ? 'binary' : 'array',
                    bookVBA: true,
                });
                /* Get first worksheet */
                const wsname = wb.SheetNames[0];
                const ws = wb.Sheets[wsname];
                /* Convert array of arrays */
                const data = XLSX.utils.sheet_to_json(ws);
                setExcelData(data);
                setFileHeader(Object.keys(data[0]));
                setModelShow(true);
         
            };
            if (rABS) {
            console.log('rABS');
            reader.readAsBinaryString(file);
            } else {
            reader.readAsArrayBuffer(file);
            }
            document.getElementsByClassName('add-multi-scheme')[0].style.display = "block";
            document.getElementsByClassName('add-multi-file')[0].style.display = "block";
        }
        catch{
            alert("Please select Valid file");
        }
      }
    function handleUserHeaderSubmit(e){
        e.preventDefault();
        var data;
        var pata;
        const Datecol = e.target.dateCol.value;
        const Pricecol = e.target.priceCol.value;
        const Modelcol = e.target.modelCol.value;
        const curr_state = {
            date: "DateCol",
            price: "PriceCol",
            model: "ModelCol"
        }
        
        
        console.log(excelData.length);
        data = excelData.map((item)=>pata = {
            DateCol : item[Datecol],
            PriceCol : item[Pricecol],
            ModelCol : item[Modelcol]
        })
        // console.log(excelJSON);
        if(!props.excelJSON){
            props.setExcelJSON(data);
        }
        else{
            props.setExcelJSON(props.excelJSON.concat(data));
        }
        
        
        props.setUserHeader(curr_state);
        setUserHeaderSubmit(true);
        setModelShow(false);
        return(<div></div>);
    }
    return(
      <div>
    <Form onSubmit = {handleActivationUpload}> 
        <Form.Group controlId="formFile" className="mb-3">
                <Form.Label>Upload Activation Sheet</Form.Label>
                <Form.Control type="file" accept={SheetJSFT} onChange={handleActivationFileChange}/>
                <Button className="activation_file_upload" id={"activation_file_upload" + props.fileNum}  variant="primary" type="submit">Upload</Button>
        </Form.Group>
    </Form>
    <Modal show={modelShow}>
    <Modal.Header>
      <Modal.Title>Please Select the Correct Attributes</Modal.Title>
    </Modal.Header>
    <Modal.Body>
        <Form onSubmit={handleUserHeaderSubmit}>
            <Form.Group className="mb-3" controlId="date_column">
                <Form.Label>Date Column</Form.Label>
                <Form.Control as="select" name="dateCol">
                {fileHeaderOptions}
                </Form.Control>
            </Form.Group>
            <Form.Group className="mb-3" controlId="model_column">
                <Form.Label>Model Column</Form.Label>
                <Form.Control as="select" name = "modelCol">
                {fileHeaderOptions}
                </Form.Control>
            </Form.Group>
            <Form.Group className="mb-3" controlId="Price_column">
                <Form.Label>Price Column</Form.Label>
                <Form.Control as="select" name ="priceCol">
                {fileHeaderOptions}
                </Form.Control>
            </Form.Group>
            <Modal.Footer>
                <Button variant="primary" type="Submit">
                    Submit 
                </Button>
            </Modal.Footer>
        </Form>
    </Modal.Body>
    
  </Modal>
  </div>
    )
};
export default UploadFileComponent;