import {Form,Button,Row,Col,Modal} from 'react-bootstrap';
import {useState,useEffect} from 'react';
import { model } from '../shared/model_data';
import { SheetJSFT } from '../shared/file_types';
import { make_cols } from './MakeColumns';
import Loading from './Loading';
import axios from "axios";
import XLSX from 'xlsx';
import {useHistory} from "react-router-dom";
import {MdAddCircle} from 'react-icons/md';
import SchemeBoxComponent from "./SchemeBoxComponent";
// var Multiselect = require('react-bootstrap-multiselect');

function UpdateScheme(props){
    const id = props.match.params.id;
    const [scheme,setScheme] = useState();
    const history = useHistory();
    const [modelList,setModelList] = useState();
    const [isUpdating,setIsUpdating] = useState(false);
    var miniSchemeArray = [];
    var loading = true;
    useEffect(() => {
        fetchSchemeData(id);
      }, []);
    async function fetchSchemeData(id){
        try{
                
            const schemeGetCall = await axios.get(`/scheme/${id}`);
            const excelData = await axios.get(`/scheme/exceldata/${id}`);
            // console.log(excelData);

            const modelCol = excelData.data.results.data_header.model;
            // const modelList = excelData.excel_data[modelCol];
            // modelList = new Set(excelData.data.results.excel_data.map(value=>value[modelCol]));
            setModelList([...new Set(excelData.data.results.excel_data.map(value=>value[modelCol]))]);
            const dataFromAPI = schemeGetCall.data.results;
            setScheme(dataFromAPI);
            // setFileHeader(dataFromAPI.data_header);
        }
        catch(e){
            console.log(e);
            alert(e.message);
        }
    }
    async function handleUpdateScheme(event) {
        setIsUpdating(true);
        event.preventDefault();
            var schemeName = [];
            var start_date = [];
            var end_date = [];
            var condType = [];
            var price_condition = [];
            var model_condition = [];
            var creditValue = [];
            var modelOptions = [];
            for (let i = 0; i < miniSchemeArray.length; i++) {
                console.log(i);
                schemeName.push(event.target["schemeName_"+i].value);
                start_date.push(event.target["startDate_"+i].value);
                end_date.push(event.target["endDate_"+i].value);
                condType.push(event.target["condType_"+i].value);
                if(event.target["condType_"+i].value === "No"){
                    price_condition.push({"operator" : null,"price" : null});
                    model_condition.push(null);
                }
                else if(event.target["condType_"+i].value === "Price_Condition"){
                    price_condition.push({"operator" : event.target["priceOperator_"+i].value,"price" : event.target["condValue_"+i].value});
                    model_condition.push(null);
                }
                else if(event.target["condType_"+i].value === "Model_Condition"){
                    price_condition.push({"operator" : null,"price" : null});
                    model_condition.push(Object.values(event.target["modelOptionsSelected_" + i]).map(item=>item.value));
                }
                creditValue.push({"creditType":event.target["creditType_" + i].value,"creditValue":event.target["creditValue_" + i ].value});
            }
            console.log(model_condition);
            try{                   
                await axios.put(`/scheme/${id}`, {
                        name: schemeName,
                        start_date: start_date,
                        end_date: end_date,
                        condition_type: condType,
                        price_condition: price_condition,
                        model_condition: model_condition,
                        creditValue:creditValue
                });
                alert('Scheme added succesfully');
                history.push("/");
            }
            catch(e){
                console.log(e);
                alert(e.message);
            }
            
        // }
   }
    if(scheme && modelList && loading){
        for (let p = 0; p < scheme.creditNote.length; p++) {
            miniSchemeArray.push(<SchemeBoxComponent scheme = {scheme} schemeNum = {p} key={p} modelList = {modelList}/>);          
        }
    return(
    <div className = "container">
        <div className="row">
            <div className="col-md-4 offset-md-4">
                <Form onSubmit={(e) => {handleUpdateScheme(e)}}>
                    <div className="scheme-wrapper">
                        {miniSchemeArray}
                    </div>

                    <Button variant="primary" type="submit" disabled={isUpdating} className="scheme-update-button">
                        {isUpdating ? "Updating" : "Update"}
                    </Button>
                    
                </Form>
            </div>
        </div>
    </div>)
    }
    else{
        return (<div><Loading /></div>);
    }
}
export default UpdateScheme;