
import {MiniScheme as ViewMiniScheme} from './ViewMiniScheme';
import {FaChevronCircleDown,FaChevronCircleUp,FaEdit} from 'react-icons/fa';
import {MdEdit,MdDelete,MdAddCircle,MdCancel} from 'react-icons/md';
import {
    Card, Button, Col, Row, Toast
  } from 'react-bootstrap';
import axios from "axios";
import { formatrs as formatRs } from './ViewMiniScheme';

function toggleView(id){
    const schemeDoc = document.getElementById(id);
    const schemeMore = schemeDoc.getElementsByClassName("more-details")[0];
    const schemeViewDown = schemeDoc.getElementsByClassName("down-icon")[0];
    const schemeViewUp = schemeDoc.getElementsByClassName("up-icon")[0];

    if(schemeMore.style.display==="none" || schemeMore.style.display==="" ){
        schemeMore.style.display="block";
        schemeViewDown.style.display = "none";
        schemeViewUp.style.display = "block";
    }
    else {
        schemeMore.style.display="none";
        schemeViewDown.style.display = "block";
        schemeViewUp.style.display = "none";
    };
    // console.log(document.getElementById(id).style.display);
}

function ViewSchemeComponent(props){
    var setDeleteLoading = props.setDeleteLoading;
    var setDeleteShow = props.setDeleteShow;
    var miniSchemeArray = [];
    var scheme = props.scheme;
    var i = scheme.name.length;
    var totalCredit = 0;
    var edit_link = '/updatescheme/' + scheme._id;
    async function deleteScheme(id){
        setDeleteLoading(true);
    
        // await sleep(2000);        
        try{
            const delete_scheme = await axios.delete(`/scheme/${id}`).then(setDeleteLoading(false)).then(setDeleteShow(true));
            props.updateSchemeData(props.schemeData.filter(item => item._id !== id));
            
        }
        catch (e) {
            console.log(e);
            alert(e.message);
        }
    }
    
    for (let p = 0; p < i; p++) {
        totalCredit = totalCredit + scheme.creditNote[p];
        miniSchemeArray.push(<ViewMiniScheme scheme = {scheme} schemeNum = {p} key={p}/>);          
    }
    console.log(totalCredit);
    return (
        <div className="major-scheme" id={scheme._id}>
            <Card>
            <Card.Header>
                {scheme._id}
                <span class="float-right">
                    <Button className="button-edit" onClick={(e) => {e.preventDefault();window.location.href=edit_link;}}><MdEdit size={20}/></Button>
                    <Button className="button-delete" onClick={() => { if (window.confirm('Are you sure you wish to delete this scheme?')) deleteScheme(scheme._id) } }><MdCancel size={20}/></Button>
                </span>
            </Card.Header>
            <Card.Body>
                <p>
                <Button className="green-background">Credit Note :  {formatRs(totalCredit)} </Button>
                <Button className = "view-more" onClick={() => toggleView(scheme._id)}><FaChevronCircleDown size={20} className="down-icon"/><FaChevronCircleUp size={20} className="up-icon"/></Button>
                </p>
                <div className="more-details">
                    {miniSchemeArray}
                </div>
            </Card.Body>
            </Card>
            
            
        </div>
    );
}
export default ViewSchemeComponent;