import {Form,Button,Row,Col,Modal} from 'react-bootstrap';
import {useState,useEffect} from 'react';
import { model } from '../shared/model_data';
import { make_cols } from './MakeColumns';
import axios from "axios";
import {MdDelete,MdCancel} from 'react-icons/md';
import {useHistory} from "react-router-dom";
import { default as ReactSelect } from "react-select";
import { components } from "react-select";
import {MdAddCircle} from 'react-icons/md';
import SchemeBoxComponent from './SchemeBoxComponent';
import UploadFileComponent from './UploadFileComponent';
// var Multiselect = require('react-bootstrap-multiselect');

function formattedDate(d = new Date) {
    let month = String(d.getMonth() + 1);
    let day = String(d.getDate());
    const year = String(d.getFullYear());
  
    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;
  
    return `${year}-${month}-${day}`;
  }

function NewSchemeForm(){
    
    
    const [userHeader,setUserHeader] = useState({
        date: '',
        model: '',
        price: ''
    });
    const [modelList,setModelList] = useState();
    const [schemeArray,setSchemeArray] = useState([]);
    const [file,setFile] = useState();
    const [schemeArr,setSchemeArr] = useState([]);
    const [fileStartDate,setFileStartDate] = useState();
    const [fileEndDate,setFileEndDate] = useState();
    const [schemeNum,setSchemeNum] = useState(0);
    
    const [schemeBox,setSchemeBox] = useState();
    const [excelJSON,setExcelJSON] = useState();
    const [fileNum,setFileNum] = useState(0);
    const [uploadFileComponentArray,setUploadFileComponentArray] = useState([]);
    const [uploadFileComponent,setUploadFileComponent] = useState();
    const history = useHistory();
    

    useEffect(()=>{
        var addExtraFile = [...uploadFileComponentArray];
        addExtraFile.push({"uploadComponent": <UploadFileComponent key = {fileNum} fileNum = {fileNum} setExcelJSON = {setExcelJSON} excelJSON = {excelJSON} setUserHeader = {setUserHeader} />});
        setFileNum(fileNum+1);
        setUploadFileComponentArray(addExtraFile);
    },[]);
    useEffect(()=>{
        if(modelList){
            if(schemeArray.length === 0){
                document.getElementById('add-multi-scheme-button').click();
            }
        }
    },[modelList]);
    useEffect(()=>{
        renderSchemeBoxes();
        setSchemeArr(schemeArray);
    },[schemeArray]);
    useEffect(()=>{
        if(uploadFileComponentArray){
            setUploadFileComponent(uploadFileComponentArray.map((add_file)=>{return(add_file.uploadComponent)}));
        };
    },[uploadFileComponentArray]);

    useEffect(()=>{
        if(excelJSON){
            const start_date = new Date('1900-01-01');
            const end_date = new Date('1900-01-01');
            const dateList = [];
            excelJSON.map(value=>dateList.push(value["DateCol"]));
            const min_date = Math.min.apply(null, dateList);
            const max_date = Math.max.apply(null, dateList);
            start_date.setDate(start_date.getDate()+min_date-2);
            end_date.setDate(end_date.getDate()+max_date-2);
            setFileStartDate(formattedDate(start_date));
            setFileEndDate(formattedDate(end_date));
            setModelList([...new Set(excelJSON.map(value=>value["ModelCol"]))]);
        }
    },[excelJSON]);
    function removeSchemeBox(id){
        console.log(id);
        // e.preventDefault();
        // console.log("Remove Button Clicked" + id);
        // console.log(schemeNum);
        console.log(schemeArray);
        // console.log(schemeArr);
        // var newSchemeArray = schemeArray;
        var newSchemeArray = [...schemeArray];
        console.log(newSchemeArray);
        newSchemeArray = newSchemeArray.filter( obj => obj.schemeNum !== id);
        console.log(newSchemeArray);
        setSchemeArray(newSchemeArray);

    }
    function addSchemeBox(e){ 
        e.preventDefault();
        if(!modelList){
            alert("Please upload the activation sheet first");
            return(<div></div>);
        }
        var scheme = [...schemeArray];
        console.log(scheme);
        scheme.push({"schemeNum" : schemeNum,"schemeComponent":<SchemeBoxComponent key={schemeNum} modelList = {modelList} startDate = {fileStartDate} endDate = {fileEndDate} schemeNum = {schemeNum} removeSchemeBox = {removeSchemeBox}/>});
        setSchemeNum(schemeNum+1);
        setSchemeArray(scheme);
        renderSchemeBoxes();
    }

    function renderSchemeBoxes(){
        console.log("Render Scheme Box is called");
        setSchemeBox(schemeArray.map((scheme)=>{
            return(
            <div>
                
                <div class="remove-scheme-wrapper float-right"><Button type="button" onClick = {(e)=> {removeSchemeBox(scheme.schemeNum);}} className = "button-remove-scheme"><MdCancel size={30}/></Button></div>
                {scheme.schemeComponent}
            </div>);
        }));
        if(schemeArray.length>0){
            document.getElementsByClassName('scheme-submit-button')[0].style.display = "block";
        }
        else{
            document.getElementsByClassName('scheme-submit-button')[0].style.display = "none";
        }
    }
    async function handleAddScheme2(event){
        event.preventDefault();
        
        // console.log(event);
        console.log(schemeNum);
        if(schemeArray.length === 0  ){
            alert("Please add a scheme first using + Button");
            return(<div></div>);
        }
        else {
            console.log(schemeArray);
            var schemeName = [];
            var start_date = [];
            var end_date = [];
            var condType = [];
            var price_condition = [];
            var model_condition = [];
            var creditValue = [];
            var modelOptions = [];
            for (let i = 0; i < schemeArray.length; i++) {
                console.log(i);
                var schemeNum = schemeArray[i]["schemeNum"];
                schemeName.push(event.target["schemeName_"+schemeNum].value);
                start_date.push(event.target["startDate_"+schemeNum].value);
                end_date.push(event.target["endDate_"+schemeNum].value);
                condType.push(event.target["condType_"+schemeNum].value);
                if(event.target["condType_"+schemeNum].value === "No"){
                    price_condition.push({"operator" : null,"price" : null});
                    model_condition.push(null);
                }
                else if(event.target["condType_"+schemeNum].value === "Price_Condition"){
                    price_condition.push({"operator" : event.target["priceOperator_"+schemeNum].value,"price" : event.target["condValue_"+schemeNum].value});
                    model_condition.push(null);
                }
                else if(event.target["condType_"+schemeNum].value === "Model_Condition"){
                    price_condition.push({"operator" : null,"price" : null});
                    var modelOptionName = "modelOptionsSelected_" + i;
                    model_condition.push(Object.values(event.target["modelOptionsSelected_" + schemeNum]).map(item=>item.value));
                }
                creditValue.push({"creditType":event.target["creditType_" + schemeNum].value,"creditValue":event.target["creditValue_" + schemeNum ].value});
            }
            try{                   
                await axios.post(`/scheme/`, {
                        name: schemeName,
                        start_date: start_date,
                        end_date: end_date,
                        condition_type: condType,
                        price_condition: price_condition,
                        model_condition: model_condition,
                        data_header:{
                            date: userHeader.date,
                            price: userHeader.price,
                            model: userHeader.model
                        },
                        excel_data:excelJSON,
                        creditValue:creditValue,
                        // excel_file:{
                        //     name:file.name,
                        //     size:file.size
                        // },
                        // fileName:file
                }).then(alert('Scheme added succesfully')).then(history.push("/"));
            }
            catch(e){
                console.log(e);
                alert(e.message);
            }
            
        }
    }
    function addExtraFile(e){
        e.preventDefault();
        var addExtraFile = uploadFileComponentArray;
        addExtraFile.push({"uploadComponent": <UploadFileComponent key = {fileNum} fileNum = {fileNum} setExcelJSON = {setExcelJSON} excelJSON = {excelJSON} setUserHeader = {setUserHeader} />});
        setFileNum(fileNum+1);
        setUploadFileComponentArray(addExtraFile);
        setUploadFileComponent(uploadFileComponentArray.map((add_file)=>{return(add_file.uploadComponent)}));
    }
    return(
    <div className = "container">
        <div className="row">
            <div className="col-md-4 offset-md-4">
            <p>Please select the Activation file first, then add the scheme details below</p>
            <Button type="button" onClick = {(e)=> {addExtraFile(e);}} className = "add-multi-file float-right">Add Extra File</Button>
            {uploadFileComponent}
            
            
        <Form onSubmit={(e) => {handleAddScheme2(e)}}>
            <div className="scheme-wrapper">
                {schemeBox}
            </div>
            <Button  type="button" onClick={(e) => {addSchemeBox(e);}} className="add-multi-scheme float-left" id="add-multi-scheme-button">Add Scheme</Button>


                <Button variant="primary" type="submit" className="scheme-submit-button float-right">
                    Submit
                </Button>
                
            </Form>
        
        
        </div>
        </div>
    </div>)
}
export default NewSchemeForm;