import {
    Card, Button, Col, Row, Toast
  } from 'react-bootstrap';
  

function datePretty(date1){
    return Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(date1)));
}

function formatRs(amount){
    var x = parseInt(amount);
    x=x.toString();
    var lastThree = x.substring(x.length-3);
    var otherNumbers = x.substring(0,x.length-3);
    if(otherNumbers != '')
        lastThree = ',' + lastThree;
    return (otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree);
}
function ViewMiniScheme(props){

    const scheme = props.scheme;
    const i = props.schemeNum;
    var editLink = "/updatescheme/"+ scheme._id;
    const model_condition = scheme.model_condition[i];
    console.log(model_condition);
    if(model_condition){
    var modelCondition = model_condition.map((item)=>{
        return(<li>{item}</li>)
    })
    }
    return (<Card style={{ width: '100%' }} className="mt-4" key={scheme._id + i}>
                
                 {/* <Card.Img variant="top" src="assets/images/special_offer.jpg" /> */}
                 <Card.Body>
                     <div className="row">
                         <div className="col-md-6">
                             <Card.Title>{scheme.name[i]}</Card.Title>
                         </div>
                         <div className="col-md-6">
                             <Card.Text>{datePretty(scheme.start_date[i])} to {datePretty(scheme.end_date[i])}</Card.Text>
                         </div>
                     </div>
                     {/* </Col> */}
                     <Button className="green-background">Credit Note :  {formatRs(scheme.creditNote[i])} </Button>
                    
                         
                     
                     <div>
                         <p><span>Offer Applied : {scheme.creditValue[i].creditValue}</span><span>{scheme.creditValue[i].creditType}</span></p>
                         <p><span>Condition Used : {scheme.condition_type[i]}</span></p>
                         <p><span>{scheme.price_condition[i].operator}  {scheme.price_condition[i].price}</span></p>
                         <p><span>{modelCondition}</span></p>
                         <p><span>Number of Mobiles : {scheme.ctMobile[i]}</span></p>
                         <p><span>Amount of Sale :  {formatRs(scheme.totalSale[i])}</span></p>
                     </div>
                    
                 </Card.Body>
             </Card>)
}
export const formatrs = formatRs;
export const MiniScheme = ViewMiniScheme;